var timing = '';
if (window.performance) {
  var pageLoadTiming = {};
  for(var k in window.performance.timing) {
    if (k !== 'toJSON') {
      pageLoadTiming[k] = window.performance.timing[k];
    }
  }

  pageLoadTiming.code = (new Date()).valueOf();
  timing = JSON.stringify(pageLoadTiming, null, 2);
}

function iOS8Minus () {
  if (/iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream) {
    return window.performance ? false : true;
  }

  return false;
}

var iOSBugfix = iOS8Minus();


function foo_(files1, files2, This) {

  var size = 0;
  if (false && files1) {
    try {
    var file_blob = files1[0];
    size = 1111;
    var newFile1 = file_blob.slice(0, 10);

    var blob = new Blob([file_blob, "blobText"], {type: "text/plain"});
    // size = file_blob.size;
    size = newFile1.size;
    // size = blob.size;
    } catch (err) {
      socket.send(err.message);
      //console.log(err);
    }
  }

  return (<div className={styles.highlight}>
    {error}
    <pre style={{fontSize: '10px'}}>{timing}</pre>

    { files2 && <h1>Files 2 {files2[0].size}</h1> }

    { files1 && <h1>Files 1 {files1[0].size}</h1> }

    {window.Worker && <h1>Hello here!</h1> }
    <a className={styles.panel} href="#" id="fileSelect"
    onclick={

      function (e) {
        var fileElem = document.getElementById("files");
        fileElem.click();
        e.preventDefault();
      }
    }
    >Select some files</a>
    { iOSBugfix &&
       (
       <div>
     <input type="file"
      // Select list of photos
      accept="image/*"
      multiple
      id="files"
      style={{display: 'none'}}
      onchange={function () {
        g_view = patch(g_view, foo(this.files, files2));
      }}
    />
    <input type="file"
     // Take a photo
     accept="image/*"
     onchange={function () {
      // console.log(this.files);
      g_view = patch(g_view, foo(files1, this.files));
    }}/><br/>
    <input type="file"
     // Take or select a video
     accept="video/*"
     onchange={function () {
      // console.log(this.files);
      g_view = patch(g_view, foo(files1, this.files));
    }}/><br/></div>

    ) ||

    (<input type="file"
      multiple
      id="files"
      style={{display: 'none'}}
      onchange={function () {
      g_view = patch(g_view, foo(this.files, files2));
    }}
    />) }

    <br/>
    Reloading works<br/>
    <input type="file"
     // Take photo or video or chose one single file.
     onchange={function () {
      // console.log(this.files);
      g_view = patch(g_view, foo(files1, this.files));
    }}/><br/>
    

  </div>);
}

var files = [{size: 0}];

var windowURL = window.URL || window.webkitURL;

var createObjectURL = windowURL.createObjectURL;

createObjectURL = createObjectURL.bind(windowURL);

var duration = 0;

function foo () {
  var preview = <div></div>;
  if (files[0].size) {
    // TODO: preview images for most formats
    const imageType = /^image\//;
    const videoType = /^video\//;
    
    if (imageType.test(files[0].type)) {
      preview = <div><img src={createObjectURL(files[0])}
        height="60"
        onload={function() {
          window.URL.revokeObjectURL(this.src);
        }}
        />
        </div>;
    } else if (videoType.test(files[0].type)) {
/*      var reader = new window.FileReader();
      reader.onload = function (evt) {
        var video = document.getElementById('video1');
        video.src = evt.target.result;
      };
      reader.readAsDataURL(files[0]);
*/

      preview = <video
        id="video1"
        width="96" height="54"
        
        // controls
        // autoplay
        //onload={function() {
        //  windowURL.revokeObjectURL(this.src);
        //}}
        src={windowURL.createObjectURL(files[0])}
        preload="metadata"
        onloadedmetadata={function() {
          windowURL.revokeObjectURL(this.src);
          var was = 0 === duration;
          duration = this.duration;
          if (was) { g_view = patch(g_view, foo()); }
        }}
        >
        {/*<source
         type={files[0].type}/>*/}
        </video>;
    } else {
      preview = <div>{files[0].type}</div>;
    }
  }

  return <div className={styles.highlight}>{preview}{files[0].size}<input type="file"
      // multiple
      id="files"
      onchange={function () {
        if (files[0].size === 0) {
          files = this.files;
        }
        g_view = patch(g_view, foo());
      }}
    />
    Hello!!
    {duration}
    </div>;
}

var snabbdom = require('snabbdom');
var patch = snabbdom.init([ // Init patch function with choosen modules
  require('snabbdom/modules/class'), // makes it easy to toggle classes
  require('snabbdom/modules/props'), // for setting properties on DOM elements
  require('snabbdom/modules/style'), // handles styling on elements with support for animations
  require('snabbdom/modules/eventlisteners'), // attaches event listeners
]);



// Patch into empty DOM element - this modifies the DOM as a side effect

var g_view = patch(container, foo());


